import React, { useEffect, useState } from 'react';
import { Html5QrcodeScanner } from 'html5-qrcode';
import { useTranslation } from 'react-i18next';

const QRScanner = ({ onScanSuccess, onScanError }) => {
    const { t } = useTranslation();
  const [scanResult, setScanResult] = useState(null);

  useEffect(() => {
    //  
    const scanner = new Html5QrcodeScanner(
      "qr-scanner", // ID ,     
      {
        fps: 10, //    
        qrbox: 250, //   
      },
      false // verbose (   )
    );

    //   
    const onScanSuccessHandler = (decodedText) => {
      setScanResult(decodedText);
      scanner.clear(); //     
      if (onScanSuccess) onScanSuccess(decodedText); //  callback-
    };

    //   
    const onScanErrorHandler = (error) => {
      console.error("  :", error);
      if (onScanError) onScanError(error); //  callback-
    };

    //  
    scanner.render(onScanSuccessHandler, onScanErrorHandler);

    //     
    return () => {
      scanner.clear();
    };
  }, [onScanSuccess, onScanError]);

  return (
    <div>
      <div id="qr-scanner"></div>
      {scanResult && <p>{t('scaned')} {scanResult}</p>}
    </div>
  );
};

export default QRScanner;